document.addEventListener('DOMContentLoaded', function() {
    crearGaleria();
});

function crearGaleria() {
    //Seleccionamos la div que tiene como clase .gallery-img
    const galeria = document.querySelector('.gallery-img');

    //Hacemos el bucle for que va a recorrer las imágenes que hay en 
    //la carpeta thumb.
    //Con el CreateElement creamos un elemento html y luego debajo
    //le asignamos que tenga el src con la ruta que le damos
    for(let i = 1; i <= 12; i++){
        const imagen = document.createElement('IMG');
        imagen.src = `build/img/thumb/${i}.webp`;
        imagen.dataset.imagenId = i;

        //Añadir la función de mostrar imagen
        imagen.onclick = ampliarImagen;

        //Creamos la lista donde van a ir las imágenes
        const lista = document.createElement('LI');
        //Añade el elemento en una lista funcionando junto al bucle for
        lista.appendChild(imagen);

        galeria.appendChild(lista);
    }
}

function ampliarImagen(e) {
    const id = parseInt(e.target.dataset.imagenId);

    //Generar la imagen
    const imagen = document.createElement('IMG');
    imagen.src = `build/img/grande/${id}.webp`;

    //Generar el overlay
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen);
    overlay.classList.add('overlay');

    //Cerrar el overlay uando se hace click fuera de la imagen
    overlay.onclick = function() {
        overlay.remove();
    }

    //Crear un boton
    const button = document.createElement('p');
    button.textContent = 'X';
    button.classList.add('btn-cerrar');

    //Añadimos el boton al final del div con nombre overlay
    overlay.appendChild(button);

    //Cerrar cuando se pulsa el boton
    button.onclick = function() {
        overlay.remove();
    }

    //Mostrar en el HTML
    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body');
}