document.addEventListener('DOMContentLoaded', function(){
    scrollNav();
    fixedNavbar();
});

function scrollNav() {
    const links = document.querySelectorAll('.navbar a');
    const button = document.querySelector('.upbutton');

    //Codigo que realziar el scroll Smooth hacia abajo en los enlaces
    links.forEach( function(link) {
        link.addEventListener('click', function(e) {
            e.preventDefault();
            const section = document.querySelector(e.target.attributes.href.value);

            section.scrollIntoView({
                behavior: 'smooth',
            });
        });
    });

    //Codigo que realizar el scroll Smooth hacia arriba en el boton de abajo
    //a la derecha
    button.addEventListener('click', function(){
        const navbar = document.querySelector('.contenido-video');
        navbar.scrollIntoView({
            behavior: 'smooth',
        });
    })
}

function fixedNavbar() {
    //Guardamos la navbar (header) en una variable
    const bar = document.querySelector('.header');

    //Registrar el Intersection Observer
    const observer = new IntersectionObserver( function(entries) {
        if(entries[0].isIntersecting) {
            bar.classList.remove('fixed');
        } else {
            bar.classList.add('fixed');
        }
    });

    //Elemento observar
    observer.observe(document.querySelector('.contenido-video'));
}